@import "../_full-cut-mixins.less";
@import "../_full-cut-variables.less";


.fn-@{cms-ns}-goto-brand{
	background: #fff;

	a{
		display: block;
		border-left: 3px solid @price-color;

		span{
			line-height: 33*@rex;
			display: inline-block;
			vertical-align: center;

			&:first-child{
				margin-left: 10*@rex;
				font-size: @font-size-lg;
				color: @price-color;
				font-weight: bold;
			}

			&:last-child{
				font-size: @font-size-sm;
				float: right;

				i{
					color: #fff;
					border-radius: 3*@rex;
					margin-right: 12*@rex;
					margin-left: 5*@rex;
					padding: 3*@rex;
					font-size: @font-size-sm;
					display: inline-block;
					background: @price-color;
				}
			}


		}
	}
}
