@import "../_full-cut-mixins.less";
@import "../_full-cut-variables.less";

.fn-@{cms-ns}-goods-list{
	background: #fff;
	position: relative;

	.item{
		padding: 2.55% 3.2%;

		.item-info{
			margin-top: 10*@rex;

			p:nth-child(1){
				.ellipsis(2);
			}

			p:nth-child(2){
				width: 100%;
				height: 22*@rex;
				overflow: hidden;

				>span{
					white-space: nowrap;
					display: inline-block;
					vertical-align: bottom;
				}

				.fn-price-lg{
					color: @price-color;
					font-weight: bold;
				}

				.fn-price-sm{
					color: #999;
					font-size: 10*@rex;
					text-decoration: line-through;

					.point{
						font-size: 10*@rex;
					}

				}

			}
		}
	}

	&:before{
			content: "";
			display: block;
			position: absolute;
			left: 50%;
			top: -7*2*@rex;
			display: block;
			margin-left: -7*@rex;
			border-width: 7*@rex;
			border-style: dashed dashed solid dashed;
			border-color: transparent transparent #fff transparent;
			z-index: 300;
		}
}
