@import "../_full-cut-mixins.less";
@import "../_full-cut-variables.less";

.fn-@{cms-ns}-store-cut{
	overflow: hidden;

	>a{
		background: #fff;
		width: 49.6%;
		float: left;

		&:nth-child(even){
			margin-left: 0.4%;
		}

		&:nth-child(odd){
			margin-right: 0.4%;
		}

		&:nth-child(n+3){
			margin-top: 6*@rex;
		}

		p{
			.ellipsis();
			padding: 10*@rex;

			&:nth-child(1){
				padding-bottom: 0;
			}

			&:nth-child(2){
				padding-top: 0;
				color: #666;
				font-size: @font-size-sm;
			}
		}


	}

	.img-box{
		&:before{
			padding-top: 200%/3.75;
		}
	}



}
