@import "../_full-cut-mixins.less";
@import "../_full-cut-variables.less";

.fn-@{cms-ns}-big-ad{
	.img-box{
		position: relative;


		&:before{
			padding-top: 300%/7.5;
		}

		.img-link{
			padding: 5*@rex 0;
			position: absolute;
			width: 550%/7.5;
			left: (100%-550%/7.5)/2;
			bottom: 10*@rex;
			background: rgba(255,255,255,0.7);
			text-align: center;
			border-radius: 80*@rex;

			p:nth-child(1){
				font-size: @font-size-xl;
			}

			p:nth-child(2){
				font-size: @font-size-base;
				color: @price-color;
			}

			i{
				position: absolute;
				font-weight: bold;
				font-size: @font-size-xl;
				right: 10*@rex;
				top: 50%;
				margin-top: @font-size-xl/2*-1
			}
		}
	}
}
