@import "../_full-cut-mixins.less";
@import "../_full-cut-variables.less";

.fn-@{cms-ns}-main-menu{
	background: #fff;

	&:after{
		content: "";
		height: 0;
		display: block;
		clear: both;
		visibility: hidden;
	}

	.img-box{
		width: 50%;
		float: left;

		&:nth-child(2n) img{
			padding-left: 0.5px;

		}

		&:nth-child(2n+1) img{
			padding-right: 0.5px;

		}

		&:nth-child(n+3){
			margin-top: 1px;
		}

		&:before{
			padding-top: 210%/3.74;
		}
	}
}
