@import "../variables";
@import "../mixins";

.cmp-rb-nav(@bottom: 2.32rem, @zindex: @zindex-fixed) {
	.fixed(_,.8rem,@bottom,_);
	z-index: @zindex;

	> .item {
		display: block;
		.size(1.6rem);
		line-height: 1.6rem;
		border:1*@rex @border-color-base solid;
		background: rgba(255,255,255, 0.8);
		border-radius: 50%;
		margin-top: 0.2rem;
		text-align: center;
		font-size: 1rem;

		&:active{
			background: rgba(245,245,245, 0.8);
		}

		&-gotop{
			display: none;
		}
	}

	.img-banner{
	    display: block;
	    .size(2.6rem);
	    margin-bottom: 5px;
	    border-radius: 2.6rem;
	    overflow: hidden;
	    margin-left: -.3rem;
	}

	.go-cart{
		margin-bottom: 5px;
    	position: relative;

    	em{
    		.absolute(11%,11%,_,_);
    		color: @body-background;
		    background: #db384c;
		    font-size: @font-size-sm;
		    .size(.64rem);
		    line-height: .64rem;
		    border-radius: 100%;
		    text-align: center;
		    .ellipsis();
		}
	}
}
