@import "../mixins";
@import "../variables";

.cmp-swiper(@paddingTop: 50%){
	.swiper-slide .img-box{
		display: block;

		&:before {
			padding-top: @paddingTop;
		}
	}

	.swiper-pagination{
		.absolute(_,0,0.3rem,_);
		z-index: 5;
		height: .16rem;
		width: 100%;
		text-align: right;

		> .swiper-pagination-bullet,.swiper-pagination-bullets{
			display: inline-block;
			vertical-align: top;
			text-align: center;
			.size(.18rem);
			margin-right: .3rem;
			border-radius: 100%;
			border: .04rem solid #fff;

			&-active{
				background: @ht-color;
				border: .02rem solid @ht-color;
			}
		}

	}
}
